:root {
  --primary-color: #ff5733;
  --primary-light: #ff8c66;
  --primary-dark: #e64d2e;
  --primary-faded: #ffe6e0;
  --text-color: #2c1810;
  --text-light: #666;
  --bg-color: #fff9f7;
  --container-bg: #fff;
  --shadow-color: #ff57331a;
  --status-bg: #f5f5f5;
  --info-bg: #e3f2fd;
  --info-text: #0d47a1;
  --info-border: #90caf9;
  --success-bg: #e8f5e9;
  --success-text: #1b5e20;
  --success-border: #81c784;
  --warning-bg: #fff3e0;
  --warning-text: #e65100;
  --warning-border: #ffb74d;
}

[data-theme="dark"] {
  --primary-color: #ff5733;
  --primary-light: #ff8c66;
  --primary-dark: #cc4629;
  --primary-faded: #331511;
  --text-color: #ffe6e0;
  --text-light: #ffb3a6;
  --bg-color: #1a0d0a;
  --container-bg: #261410;
  --shadow-color: #ff573333;
  --status-bg: #2a2a2a;
  --info-bg: #1a237e;
  --info-text: #bbdefb;
  --info-border: #3949ab;
  --success-bg: #1b5e20;
  --success-text: #c8e6c9;
  --success-border: #2e7d32;
  --warning-bg: #e65100;
  --warning-text: #ffe0b2;
  --warning-border: #f57c00;
}

body {
  background-color: var(--bg-color);
  color: var(--text-color);
  margin: 0;
  padding: 20px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  transition: background-color .3s, color .3s;
}

.theme-toggle {
  position: fixed;
  top: 20px;
  right: 20px;
}

.theme-toggle button {
  color: var(--text-color);
  cursor: pointer;
  background: none;
  border: none;
  border-radius: 50%;
  padding: 10px;
  font-size: 1.5em;
  transition: background-color .3s;
}

.theme-toggle button:hover {
  background-color: var(--shadow-color);
}

h1 {
  background-color: var(--primary-color);
  text-align: center;
  color: #fff;
  box-shadow: 0 2px 4px var(--shadow-color);
  border-radius: 8px;
  margin: 0 0 20px;
  padding: 20px;
}

.container {
  background-color: var(--container-bg);
  max-width: 800px;
  box-shadow: 0 4px 6px var(--shadow-color);
  border-radius: 12px;
  margin: 0 auto;
  padding: 25px;
}

.tabs {
  gap: 10px;
  margin-bottom: 20px;
  display: flex;
}

.tab-btn {
  border: 2px solid var(--primary-color);
  color: var(--text-color);
  cursor: pointer;
  background-color: #0000;
  border-radius: 6px;
  padding: 10px 20px;
  transition: all .3s;
}

.tab-content {
  display: block;
}

.tab-content.hidden {
  display: none;
}

.input-group {
  justify-content: space-between;
  gap: 20px;
  margin-bottom: 25px;
  display: flex;
}

.dimension-input {
  flex: 1;
}

label {
  color: var(--text-color);
  margin-bottom: 8px;
  font-weight: 500;
  display: block;
}

input, select {
  border: 2px solid var(--primary-faded);
  box-sizing: border-box;
  background-color: var(--container-bg);
  width: 100%;
  color: var(--text-color);
  border-radius: 6px;
  padding: 10px;
  transition: border-color .3s;
}

input:focus, select:focus {
  border-color: var(--primary-color);
  box-shadow: 0 0 0 2px var(--primary-faded);
}

select {
  width: auto;
}

.controls {
  justify-content: center;
  gap: 15px;
  margin-bottom: 25px;
  display: flex;
}

button {
  background-color: var(--primary-color);
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 6px;
  align-items: center;
  gap: 8px;
  padding: 12px 24px;
  transition: all .3s;
  display: flex;
}

button:hover {
  background-color: var(--primary-dark);
  transform: translateY(-2px);
}

button i {
  font-size: .9em;
}

.toggle-button.active {
  background-color: var(--primary-dark);
}

.presets {
  text-align: center;
  margin: 25px 0;
}

.preset {
  background: var(--bg-color);
  border: 1px solid var(--primary-faded);
  cursor: pointer;
  text-align: center;
  color: var(--text-color);
  border-radius: 4px;
  padding: 8px 12px;
  transition: all .3s;
  position: relative;
}

.preset:hover {
  background: var(--primary-faded);
  border-color: var(--primary-color);
  color: var(--text-color);
}

.preview-container {
  text-align: center;
  margin: 25px 0;
}

.result-container {
  text-align: center;
  background-color: var(--container-bg);
  box-shadow: 0 2px 4px var(--shadow-color);
  border-radius: 8px;
  margin-top: 25px;
  padding: 15px;
}

.result-box {
  background-color: var(--bg-color);
  border-radius: 8px;
  min-width: 200px;
  padding: 20px;
  display: inline-block;
}

#result, #decimal {
  color: var(--text-color);
  margin: 8px 0;
  font-weight: 500;
}

.tooltip {
  background-color: var(--primary-dark);
  color: #fff;
  pointer-events: none;
  opacity: 0;
  box-shadow: 0 2px 4px var(--shadow-color);
  z-index: 1000;
  border-radius: 4px;
  padding: 8px 12px;
  font-size: .9em;
  transition: opacity .3s;
  position: fixed;
}

.converter-container {
  background-color: var(--bg-color);
  border-radius: 8px;
  padding: 20px;
}

.scale-controls {
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin: 20px 0;
  display: flex;
}

.scale-controls input {
  width: 100px;
}

.scale-result {
  text-align: center;
  margin-top: 20px;
  font-weight: 500;
}

@media (width <= 600px) {
  .input-group {
    flex-direction: column;
  }

  .controls {
    flex-wrap: wrap;
  }

  .preset-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

.info-section {
  color: var(--text-color);
  padding: 20px;
}

.info-section h2 {
  color: var(--primary-color);
  margin-bottom: 20px;
}

.info-card {
  background-color: var(--bg-color);
  box-shadow: 0 2px 4px var(--shadow-color);
  border-radius: 8px;
  margin-bottom: 20px;
  padding: 20px;
}

.info-card h3 {
  color: var(--primary-color);
  margin-bottom: 15px;
}

.info-card ul, .info-card ol {
  margin-bottom: 15px;
  padding-left: 20px;
}

.info-card li {
  margin-bottom: 8px;
  line-height: 1.5;
}

.example-box {
  background-color: var(--container-bg);
  border-left: 4px solid var(--primary-color);
  border-radius: 0 8px 8px 0;
  margin-top: 15px;
  padding: 15px;
}

.example-box p {
  margin: 5px 0;
}

.ratio-grid {
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 20px;
  padding: 20px;
  display: grid;
}

.ratio-card {
  background-color: var(--bg-color);
  text-align: center;
  box-shadow: 0 2px 4px var(--shadow-color);
  border-radius: 8px;
  padding: 20px;
}

.ratio-card h3 {
  color: var(--primary-color);
  margin-bottom: 15px;
}

.ratio-card h4 {
  color: var(--text-color);
  margin: 15px 0;
}

.ratio-card ul {
  text-align: left;
  margin-top: 15px;
  padding: 0;
  list-style: none;
}

.ratio-card li {
  margin: 5px 0;
  padding-left: 20px;
  position: relative;
}

.ratio-card li:before {
  content: "â€¢";
  color: var(--primary-color);
  position: absolute;
  left: 5px;
}

.ratio-preview {
  background-color: var(--primary-light);
  border: 2px solid var(--primary-color);
  max-width: 200px;
  margin: 15px auto;
}

.ratio-16-9 {
  aspect-ratio: 16 / 9;
}

.ratio-4-3 {
  aspect-ratio: 4 / 3;
}

.ratio-21-9 {
  aspect-ratio: 21 / 9;
}

.ratio-1-1 {
  aspect-ratio: 1;
}

.ratio-3-2 {
  aspect-ratio: 3 / 2;
}

.ratio-9-16 {
  aspect-ratio: 9 / 16;
  max-width: 112.5px;
}

@media (width <= 768px) {
  .ratio-grid {
    grid-template-columns: 1fr;
  }

  .info-card {
    padding: 15px;
  }

  .example-box {
    padding: 10px;
  }
}

.tab-content {
  opacity: 1;
  transition: opacity .3s ease-in-out;
}

.tab-content.hidden {
  opacity: 0;
  display: none;
}

.main-header {
  background-color: var(--container-bg);
  border-bottom: 2px solid var(--primary-faded);
  box-shadow: 0 2px 4px var(--shadow-color);
  z-index: 1000;
  position: sticky;
  top: 0;
}

.nav-container {
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 1rem;
  display: flex;
}

.logo {
  color: var(--primary-color);
  cursor: default;
  -webkit-user-select: none;
  user-select: none;
  align-items: center;
  gap: 10px;
  font-size: 1.5rem;
  font-weight: bold;
  display: flex;
}

.logo i {
  transition: transform .3s;
}

.logo:hover i {
  animation: 1s infinite bounce;
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-10px);
  }
}

.logo a {
  color: var(--text-color);
  align-items: center;
  gap: 10px;
  text-decoration: none;
  display: flex;
}

.logo a:hover {
  color: var(--primary-color);
}

.nav-links {
  gap: 2rem;
  display: flex;
}

.nav-links a {
  color: var(--text-color);
  border-radius: 4px;
  padding: .5rem 1rem;
  text-decoration: none;
  transition: all .3s;
}

.nav-links a:hover, .nav-links a.active {
  color: var(--primary-color);
  background-color: var(--bg-color);
}

.main-footer {
  background-color: var(--primary-faded);
  border-top: 2px solid var(--primary-light);
  color: var(--text-color);
  margin-top: 2rem;
  padding: 1.5rem 0 .5rem;
}

.footer-content {
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1rem;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
  display: grid;
}

.footer-section h3 {
  color: var(--primary-dark);
  margin-bottom: .5rem;
  font-size: 1rem;
}

.footer-section ul {
  padding: 0;
  list-style: none;
}

.footer-section ul li {
  margin-bottom: .25rem;
}

.footer-section a {
  color: var(--text-color);
  text-decoration: none;
  transition: color .3s;
}

.footer-section a:hover {
  color: var(--primary-color);
}

.social-links {
  gap: 1rem;
  margin-top: 1rem;
  display: flex;
}

.social-links a {
  font-size: 1.5rem;
}

.newsletter-form {
  gap: .5rem;
  margin-top: 1rem;
  display: flex;
}

.newsletter-form input {
  flex: 1;
  min-width: 0;
}

.footer-bottom {
  border-top: 1px solid var(--shadow-color);
  text-align: center;
  max-width: 1200px;
  margin: 1rem auto 0;
  padding: .5rem;
}

.footer-links {
  margin-bottom: .5rem;
}

.footer-links a {
  color: var(--text-color);
  margin: 0 .5rem;
  font-size: .8rem;
  text-decoration: none;
}

.copyright {
  color: var(--text-color);
  opacity: .8;
  font-size: .8rem;
}

@media (width <= 768px) {
  .footer-content {
    grid-template-columns: 1fr;
    gap: .5rem;
  }

  .footer-section {
    margin-bottom: .5rem;
  }

  .footer-links a {
    margin: .25rem 0;
    display: block;
  }
}

.modal {
  z-index: 1000;
  background-color: #00000080;
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.modal-content {
  background-color: var(--container-bg);
  border-radius: 8px;
  width: 90%;
  max-width: 800px;
  max-height: 90vh;
  margin: 5% auto;
  padding: 2rem;
  overflow-y: auto;
}

.modal-header {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  display: flex;
}

.close-modal {
  cursor: pointer;
  color: var(--text-color);
  background: none;
  border: none;
  font-size: 1.5rem;
}

.cookie-consent {
  background-color: var(--container-bg);
  box-shadow: 0 -2px 4px var(--shadow-color);
  z-index: 999;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  display: flex;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
}

.cookie-buttons {
  gap: 1rem;
  display: flex;
}

.btn-primary, .btn-secondary {
  cursor: pointer;
  border: none;
  border-radius: 4px;
  padding: .5rem 1rem;
}

.btn-primary {
  background-color: var(--primary-color);
  color: #fff;
  transition: background-color .3s;
}

.btn-primary:hover {
  background-color: var(--primary-light);
}

.btn-secondary {
  background-color: var(--primary-faded);
  color: var(--primary-dark);
  transition: all .3s;
}

.btn-secondary:hover {
  background-color: var(--primary-light);
  color: #fff;
}

@media (width <= 768px) {
  .nav-links {
    display: none;
  }

  .menu-btn {
    display: block;
  }

  .footer-content {
    grid-template-columns: 1fr;
  }

  .footer-links a {
    margin: .5rem 0;
    display: block;
  }

  .cookie-consent {
    text-align: center;
    flex-direction: column;
    gap: 1rem;
  }
}

:focus {
  outline: 2px solid var(--primary-color);
  outline-offset: 2px;
}

.sr-only {
  clip: rect(0, 0, 0, 0);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.content-section {
  opacity: 1;
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  transition: opacity .3s;
}

.content-section.hidden {
  opacity: 0;
  display: none;
}

.nav-link {
  position: relative;
}

.nav-link:after {
  content: "";
  background-color: var(--primary-color);
  width: 0;
  height: 2px;
  transition: width .3s;
  position: absolute;
  bottom: -2px;
  left: 0;
}

.nav-link.active:after, .nav-link:hover:after {
  width: 100%;
}

.tools-grid {
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
  display: grid;
}

.tool-card {
  background-color: var(--container-bg);
  box-shadow: 0 2px 4px var(--shadow-color);
  border: 1px solid var(--primary-faded);
  border-radius: 8px;
  padding: 1.5rem;
  transition: transform .3s;
}

.tool-card:hover {
  border-color: var(--primary-color);
  box-shadow: 0 4px 12px var(--shadow-color);
  transform: translateY(-5px);
}

.newsletter-form {
  position: relative;
}

.newsletter-form input {
  width: 100%;
  padding: .75rem 120px .75rem .75rem;
}

.newsletter-form button {
  background-color: var(--primary-color);
  color: #fff;
  height: 100%;
  padding: 0 1.5rem;
  position: absolute;
  top: 0;
  right: 0;
}

.newsletter-form button:hover {
  background-color: var(--primary-light);
}

.cookie-consent {
  transition: transform .3s;
  transform: translateY(100%);
}

.cookie-consent.show {
  transform: translateY(0);
}

.main-header, .calculator-container {
  content-visibility: auto;
  contain-intrinsic-size: auto 500px;
}

@media (prefers-reduced-motion: reduce) {
  * {
    scroll-behavior: auto !important;
    transition-duration: .01ms !important;
    animation-duration: .01ms !important;
    animation-iteration-count: 1 !important;
  }
}

.nav-link.active {
  color: var(--primary-color);
}

.tab-btn.active {
  background-color: var(--primary-color);
  color: #fff;
}

.loading {
  border: 2px solid var(--primary-faded);
  border-top: 2px solid var(--primary-color);
}

.success {
  color: #2e8b57;
  background-color: #e8f5e9;
}

.error {
  color: #d32f2f;
  background-color: #ffebee;
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 #ff573366;
  }

  70% {
    box-shadow: 0 0 0 10px #ff573300;
  }

  100% {
    box-shadow: 0 0 #ff573300;
  }
}

.badge {
  background-color: var(--primary-faded);
  color: var(--primary-dark);
}

.badge.active {
  background-color: var(--primary-color);
  color: #fff;
}

::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: var(--primary-faded);
}

::-webkit-scrollbar-thumb {
  background: var(--primary-color);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--primary-dark);
}

.ad-container {
  background: var(--bg-color);
  text-align: center;
  z-index: 1;
  border-radius: 8px;
  max-width: 100%;
  margin: 2rem 0;
  padding: 1rem;
  position: relative;
  overflow: hidden;
  box-shadow: 0 2px 4px #0000001a;
}

.ad-container ins {
  max-width: 100%;
  min-height: 90px;
  margin: 0 auto;
  display: block;
}

.ad-sidebar {
  z-index: 100;
  width: 160px;
  position: fixed;
  top: 50%;
  transform: translateY(-50%);
}

.ad-left {
  left: 20px;
}

.ad-right {
  right: 20px;
}

.container {
  z-index: 2;
  max-width: 800px;
  margin: 0 auto;
  padding: 0 20px;
  position: relative;
}

@media (width <= 1200px) {
  .ad-sidebar {
    display: none;
  }
}

.ad-bottom {
  clear: both;
  max-width: 728px;
  margin: 20px auto;
}

.ad-container:before {
  content: "Advertisement";
  text-align: center;
  color: var(--text-light);
  margin-bottom: 5px;
  font-size: 12px;
  display: block;
}

.presets-container {
  background: var(--container-bg);
  box-shadow: 0 2px 4px var(--shadow-color);
  border-radius: 8px;
  align-items: flex-start;
  gap: 20px;
  margin: 20px 0;
  padding: 20px;
  display: flex;
}

.preset-section, .custom-ratio-section {
  flex: 1;
  min-width: 0;
}

.divider {
  background: var(--primary-faded);
  align-self: stretch;
  width: 1px;
  margin: 0 10px;
}

.preset-grid {
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
  margin-top: 10px;
  display: grid;
}

.preset {
  background: var(--bg-color);
  border: 1px solid var(--primary-faded);
  cursor: pointer;
  text-align: center;
  border-radius: 4px;
  padding: 8px 12px;
  transition: all .3s;
}

.preset:hover {
  background: var(--primary-faded);
  border-color: var(--primary-color);
}

.preset:hover:after {
  content: attr(title);
  background: var(--primary-dark);
  color: #fff;
  white-space: nowrap;
  z-index: 1000;
  box-shadow: 0 2px 4px var(--shadow-color);
  border-radius: 4px;
  margin-bottom: 5px;
  padding: 6px 10px;
  font-size: .9em;
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
}

.custom-ratio-inputs {
  align-items: center;
  gap: 10px;
  margin-top: 10px;
  display: flex;
}

.custom-ratio-inputs input {
  border: 1px solid var(--primary-faded);
  background: var(--bg-color);
  width: 80px;
  color: var(--text-color);
  border-radius: 4px;
  padding: 8px;
}

.custom-ratio-inputs span {
  color: var(--text-color);
  font-weight: bold;
}

@media (width <= 768px) {
  .presets-container {
    flex-direction: column;
  }

  .divider {
    width: 100%;
    height: 1px;
    margin: 10px 0;
  }

  .preset-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

.policy-content {
  max-width: 1200px;
  margin: 2rem auto;
  padding: 0 1rem;
}

.policy-container {
  z-index: 1;
  background: var(--container-bg);
  border-radius: 8px;
  max-width: 800px;
  margin: 2rem auto;
  padding: 2rem;
  position: relative;
  box-shadow: 0 2px 4px #0000001a;
}

.policy-container h1 {
  color: var(--primary-color);
  text-align: left;
  box-shadow: none;
  background: none;
  margin-bottom: 1.5rem;
  padding: 0;
}

.policy-container .last-updated {
  color: var(--text-light);
  margin-bottom: 2rem;
  font-size: .9rem;
  font-style: italic;
}

.policy-container section {
  margin-bottom: 2rem;
}

.policy-container section:last-child {
  margin-bottom: 0;
}

.policy-container h2 {
